const videoBlock = document.querySelector('.video-block');

if(videoBlock){
  const video = videoBlock.querySelector('video');
  const playBtn = videoBlock.querySelector('.video-block__play');

  playBtn.addEventListener('click', () => {
    playBtn.classList.add('video-block__play--active');
    videoBlock.classList.add('video-block--active');
    video.play();
    video.controls = true;
  });

  video.onpause = function() {
    playBtn.classList.remove('video-block__play--active');
    videoBlock.classList.remove('video-block--active');
    video.controls = false;
  }

}

